<template>
  <svg
    ref="svg-temp"
    class="svg-temp"
    :view-box.camel="viewbox"
    xmlns="http://www.w3.org/2000/svg"
  >
    <ChartsDayItem
      v-for="(points, index) in dataPoints"
      :key="`ch-${index}`"
      :datasetChart="points"
    />
    <ChartsDayTextItem
      v-for="(value, index) in dataPoints"
      :key="`tx-${index}`"
      :datasetChart="value"
      :textSize="textSize"
      :marginText="marginText"
    />
  </svg>
</template>

<script>
import ChartsDayItem from "./ChartsDayItem.vue";
import ChartsDayTextItem from "./ChartsDayTextItem.vue";
export default {
  components: {
    ChartsDayItem,
    ChartsDayTextItem,
  },
  data() {
    return {
      /**
       *   SVG.
       */
      width: 300,
      /**
       *   SVG.
       */
      height: 170,
      /**
       *    .
       */
      textSize: 16,
      /**
       *    .
       */
      marginFromCell: 8,
      /**
       *    .
       */
      marginText: 5,
      /**
       *     .
       */
      circleRadius: 5.5,
    };
  },
  mounted() {
    /**
     *      , 
     *         data:
     * width  height.
     */
    this.resizeBrowserHandler();
    /**
     *     resize,   
     *   .    .
     */
    window.addEventListener("resize", this.resizeBrowserHandler);
  },
  destroyed() {
    /**
     *     resize   .
     */
    window.removeEventListener("resize", this.resizeBrowserHandler);
  },
  computed: {
    /**
     *     ,   
     *       .
     */
    tenDay() {
      return this.$store.getters.tenDaysTabTempCharts;
    },
    viewbox() {
      return `0 0 ${this.width} ${this.height}`;
    },
    /**
     *     
     *  .
     */
    totalYMargin() {
      return (
        this.textSize +
        this.marginFromCell +
        this.marginText +
        this.circleRadius / 2
      );
    },
    /**
     *   ,   ,
     * ,     
     *     .
     * @example
     *[
     *  {
     *    descr: "min",
     *    unit: "",
     *    dataset: [
     *      { x: 27.35, y: 85, temp: 11 },
     *      { x: 82.05, y: 77, temp: 12 },
     *    ],
     *  },
     *  {
     *    descr: "max",
     *    unit: "",
     *    dataset: [
     *      { x: 27.35, y: 32, temp: 18 },
     *      { x: 82.05, y: 39, temp: 17 },
     *    ],
     *  },
     *];
     */
    dataPoints() {
      const colCount = this.tenDay[0].value.length;
      const arr = this.tenDay.map((item) => {
        let x = this.width / (colCount * 2);

        const dataset = item.value.reduce((total, current, index) => {
          if (current !== undefined && current !== null) {
            let x1 = index === 0 ? x : 2 * x * index + x;
            const obj = {
              x: x1,
              y: this.transformYToSVG(current, item),
              temp: current,
            };
            total.push(obj);
          }
          return total;
        }, []);

        return { descr: item.descr, unit: item.unit, dataset: dataset };
      });
      return arr;
    },
  },
  methods: {
    /**
     *        svg
     *       .
     * @param temp -  .
     * @param obj -      
     *    .
     */
    transformYToSVG(temp, obj) {
      const { max, min } = obj;
      const totalYMargin =
        this.textSize +
        this.marginFromCell +
        this.marginText +
        this.circleRadius / 2;
      const y = Math.round(
        ((this.height - 2 * totalYMargin) * (max - temp)) / (max - min) +
          totalYMargin
      );
      return y;
    },
    /**
     *   ,     .
     */
    resizeBrowserHandler() {
      /**
       *        .
       * @param element -     $refs    
       * .
       */
      const getWidth = (element) => {
        return Math.round(this.$refs[element].getBoundingClientRect().width);
      };
      const getHeight = (element) => {
        return Math.round(this.$refs[element].getBoundingClientRect().height);
      };
      this.width = getWidth("svg-temp");
      this.height = getHeight("svg-temp");
    },
  },
};
</script>

<style lang="scss" scoped>
.svg-temp {
  display: inline-block;
  fill: none;
  width: 100%;
  height: 100%;
  // box-shadow: 0 0 0 1px teal;
}
.text-meter {
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  fill: #333333;
}
.color-path {
  stroke: #0bc2ff;
}
.dotted {
  stroke-dasharray: 8 4;
}
.thin {
  font-weight: 300;
}
</style>
